import * as PIXI from "pixi.js";
import { BaseLayer } from "./BaseLayer";
import apiManager from "../../../manager/APIManager";
import dataManager from "../../../manager/DataManager";
import { gameApp } from "../../game";

export class TopLayer extends BaseLayer {

    spr_logo        = null;

    btn_connect     = null;
    btn_cashier     = null;
    btn_help        = null;

    lbl_players     = null;

    init(resource) {
        super.init(resource);

        this.spr_logo = this.addChild(this.createSprite(resource.logo, 87.5, 55.0, 0.5, 0.5, 0.5));

        const style = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 46,
            fill: '0xFF081F'
        });
        this.lbl_players = this.addChild(this.createText('PLAYERS ONLINE:0', style, this.canvas_width / 2 - 206.0, 55.0, 0.0, 0.5));
        this.btn_connect = this.addChild(this.createButton(resource.btn_connect, this.canvas_width - 94.0, 55.0, 0.5, 0.5, 1.0, this.onConnectWallet));
        this.btn_cashier = this.addChild(this.createButton(resource.btn_cashier, this.canvas_width - 94.0, 55.0, 0.5, 0.5, 1.0, this.onCashier));
        this.btn_help = this.addChild(this.createButton(resource.btn_help, this.canvas_width - 207.5, 55.0, 0.5, 0.5, 0.5, this.onHelp));

        this.btn_cashier.visible = false;
    }

    onResize(width, height) {
        super.onResize(width, height);

        this.updateRenderer();
    }

    updateRenderer() {
        if(this.spr_logo !== null) {
            this.spr_logo.setTransform(87.5, 55.0);
            this.spr_logo.scale.set(0.5);
        }

        if(this.lbl_players !== null)
            this.lbl_players.setTransform(this.canvas_width / 2 - 206.0, 55.0);

        if(this.btn_connect !== null)
            this.btn_connect.setTransform(this.canvas_width - 94.0, 55.0);

        if(this.btn_cashier !== null)
            this.btn_cashier.setTransform(this.canvas_width - 94.0, 55.0);

        if(this.btn_help !== null) {
            this.btn_help.setTransform(this.canvas_width - 207.5, 55.0);
            this.btn_help.scale.set(0.5);
        }
    }

    onConnectWallet = async() => {
        const result = await apiManager.userConnect();
        this.btn_connect.visible = !result;
        this.btn_cashier.visible = result;
    }

    onCashier = () => {
        gameApp.showPayment();
    }

    onHelp = () => {
        gameApp.showHelp();
    }

    updateOnlineUsers = () => {
        this.lbl_players.text = 'PLAYERS ONLINE:' + dataManager.getOnlineUsers();
    }
}