import * as PIXI from "pixi.js";
import { BaseLayer } from "./BaseLayer";
import { GAME_WIDTH, GAME_HEIGHT, RESULT } from "../data/Constant";
import { gameApp } from "../../game";

export class ResultLayer extends BaseLayer {

    spr_bg      = null;
    spr_win     = null;
    spr_lose    = null;

    lbl_count   = null;
    lbl_token   = null;
    lbl_try     = null;
    lbl_waiting = null;

    btn_hint    = null;

    init(resource) {
        super.init(resource);

        this.spr_bg = this.addChild(this.createSprite(resource.result_bg, this.canvas_width / 2, this.canvas_height / 2 - 16.0, 0.5, 0.5, 0.5));
        this.spr_win = this.addChild(this.createSprite(resource.title_win, this.canvas_width / 2, this.canvas_height / 2 - 135.0, 0.5, 0.5, 0.415));
        this.spr_lose = this.addChild(this.createSprite(resource.title_lose, this.canvas_width / 2, this.canvas_height / 2 - 135.0, 0.5, 0.5, 0.415));

        const style1 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 90,
            fill: '0xFF081F',
            stroke: '0xFFFFFF',
            strokeThickness: 1
        });
        this.lbl_count = this.addChild(this.createText('1000', style1, this.canvas_width / 2, this.canvas_height / 2 - 43.5, 0.5, 0.5));

        const style2 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 52,
            fill: '0xFFFFFF'
        });
        this.lbl_token = this.addChild(this.createText('TOKEN', style2, this.canvas_width / 2, this.canvas_height / 2 + 36.0, 0.5, 0.5));

        const style3 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 69,
            fill: '0xFF081F',
            stroke: '0xFFFFFF',
            strokeThickness: 1
        });
        this.lbl_try = this.addChild(this.createText('TRY AGAIN!', style3, this.canvas_width / 2, this.canvas_height / 2 - 24.0, 0.5, 0.5));

        const style4 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 36,
            fill: '0xFF081F',
            stroke: '0xFFFFFF',
            strokeThickness: 1,
            align: 'center'
        });
        this.lbl_waiting = this.addChild(this.createText('WAITING FOR\nTHE NEXT ROUND', style4, this.canvas_width / 2, this.canvas_height / 2 - 15.0, 0.5, 0.5));

        this.btn_hint = this.addChild(this.createButton(resource.btn_hint, this.canvas_width / 2 + 207.0, this.canvas_height / 2 + 108.5, 0.5, 0.5, 0.5, this.onHint));

        this.hide();
    }

    onResize(width, height) {
        super.onResize(width, height);

        this.updateRenderer();
    }

    updateRenderer() {
        const scale_x = this.canvas_width / GAME_WIDTH;
        const scale_y = this.canvas_height / GAME_HEIGHT;
        const scale = Math.min(scale_x, scale_y);

        if(this.spr_bg !== null) {
            this.spr_bg.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 16.0 * scale);
            this.spr_bg.scale.set(0.5 * scale);
        }

        if(this.spr_win !== null) {
            this.spr_win.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 150.0 * scale);
            this.spr_win.scale.set(0.415 * scale);
        }

        if(this.spr_lose !== null) {
            this.spr_lose.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 135.0 * scale);
            this.spr_lose.scale.set(0.415 * scale);
        }

        if(this.lbl_count !== null) {
            this.lbl_count.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 43.5 * scale);
            this.lbl_count.scale.set(scale);
        }

        if(this.lbl_token !== null) {
            this.lbl_token.setTransform(this.canvas_width / 2, this.canvas_height / 2 + 36.0 * scale);
            this.lbl_token.scale.set(scale);
        }

        if(this.lbl_try !== null) {
            this.lbl_try.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 24.0 * scale);
            this.lbl_try.scale.set(scale);
        }

        if(this.lbl_waiting !== null) {
            this.lbl_waiting.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 15.0 * scale);
            this.lbl_waiting.scale.set(scale);
        }

        if(this.btn_hint !== null) {
            this.btn_hint.setTransform(this.canvas_width / 2 + 207.0 * scale, this.canvas_height / 2 + 108.5 * scale);
            this.btn_hint.scale.set(0.5 * scale);
        }
    }

    show(result, value) {
        this.spr_bg.visible = true;
        this.btn_hint.visible = true;

        switch(result) {
            case RESULT.WIN:
            this.lbl_count.text = value.toFixed(2);
                this.spr_win.visible = true;
                this.lbl_count.visible = true;
                this.lbl_token.visible = true;
                break;
            case RESULT.LOSE:
                this.spr_lose.visible = true;
                this.lbl_try.visible = true;
                break;
            case RESULT.WATCH:
                this.lbl_waiting.visible = true;
                break;
        }
    }

    hide() {
        this.spr_bg.visible = false;
        this.spr_win.visible = false;
        this.spr_lose.visible = false;
        this.lbl_count.visible = false;
        this.lbl_token.visible = false;
        this.lbl_try.visible = false;
        this.lbl_waiting.visible = false;
        this.btn_hint.visible = false;
    }

    onHint = () => {
        gameApp.showHint();
    }
}