import * as PIXI from "pixi.js";
import toast from 'react-hot-toast';
import { BaseLayer } from "./BaseLayer";
import { GAME_HEIGHT, GAME_WIDTH } from "../data/Constant";
import { EditBox } from "../widget/EditBox";
import dataManager from "../../../manager/DataManager";
import apiManager from "../../../manager/APIManager";
import { gameApp } from "..";

const PAYMENT = {
    DEPOSIT: 0,
    WITHDRAW: 1
}

export class PaymentLayer extends BaseLayer {

    spr_bg              = null;
    spr_deposit_act     = null;
    spr_withdraw_act    = null;
    spr_bg_address      = null;
    spr_bg_token        = null;
    spr_token           = null;
    spr_ether           = null;
    spr_symbol          = null;
    spr_deposit_desc    = null;
    spr_withdraw_desc   = null;
    spr_withdraw_amount = null;

    lbl_deposit         = null;
    lbl_withdraw        = null;
    lbl_deposit_address = null;

    input_address       = null;
    input_amount        = null;

    btn_deposit         = null;
    btn_withdraw        = null;
    btn_withdraw_send   = null;
    btn_copy_address    = null;
    btn_cancel          = null;

    mode                = PAYMENT.DEPOSIT;

    init(resource) {
        super.init(resource);

        this.spr_bg = this.addChild(this.createSprite(resource.bg_payment, this.canvas_width / 2, this.canvas_height / 2 - 55.5, 0.5, 0.5));
        this.spr_deposit_act = this.addChild(this.createSprite(resource.deposit_act, this.canvas_width / 2 - 124.0, this.canvas_height / 2 - 190.0, 0.5, 0.5));
        this.spr_withdraw_act = this.addChild(this.createSprite(resource.withdraw_act, this.canvas_width / 2 + 126.0, this.canvas_height / 2 - 190.0, 0.5, 0.5));
        this.spr_bg_address = this.addChild(this.createSprite(resource.rect_big, this.canvas_width / 2, this.canvas_height / 2 - 42.5, 0.5, 0.5));
        this.spr_bg_token = this.addChild(this.createSprite(resource.rect_small, this.canvas_width / 2 - 157.5, this.canvas_height / 2 + 33.5, 0.5, 0.5));
        this.spr_token = this.addChild(this.createSprite(resource.coin, this.canvas_width / 2 - 255.0, this.canvas_height / 2 + 33.5, 0.5, 0.5));
        this.spr_ether = this.addChild(this.createSprite(resource.ether, this.canvas_width / 2 - 268.0, this.canvas_height / 2 - 110.0, 0.5, 0.5));
        this.spr_symbol = this.addChild(this.createSprite(resource.symbol_1, this.canvas_width / 2 - 230.5, this.canvas_height / 2 + 33.5, 0.5, 0.5));
        this.spr_deposit_desc = this.addChild(this.createSprite(resource.text_deposit_desc, this.canvas_width / 2 - 193.5, this.canvas_height / 2 - 24.5, 0.5, 0.5));
        this.spr_withdraw_desc = this.addChild(this.createSprite(resource.text_withdraw_desc, this.canvas_width / 2 - 193.5, this.canvas_height / 2 - 74.5, 0.5, 0.5));
        this.spr_withdraw_amount = this.addChild(this.createSprite(resource.text_withdraw_amount, this.canvas_width / 2 - 213.0, this.canvas_height / 2 + 1.0, 0.5, 0.5));
        
        const style1 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 24,
            fill: '0xFF0000'
        });
        this.lbl_deposit = this.addChild(this.createText('DEPOSIT $FOOD', style1, this.canvas_width / 2 - 245.0, this.canvas_height / 2 - 60.0, 0.0, 0.5));
        this.lbl_withdraw = this.addChild(this.createText('WITHDRAW $FOOD', style1, this.canvas_width / 2 - 245.0, this.canvas_height / 2 - 110.0, 0.0, 0.5));
        
        const style2 = new PIXI.TextStyle({
            fontFamily: 'Alphakind',
            fontStyle: 'normal',
            fontSize: 16,
            fill: '000000'
        });
        this.lbl_deposit_address = this.addChild(this.createText('0X123129DO3DLS0SDLKGJKS;DGSKLDLD;GO', style2, this.canvas_width / 2 - 269.0, this.canvas_height / 2 + 7.5, 0.0, 0.5));
        
        this.input_address = new EditBox({
            fontFamily: 'Alphakind',
            fontSize: 16,
            posX: this.canvas_width / 2 - 269.0,
            posY: this.canvas_height / 2 - 65.0,
            width: 540,
            height: 45,
            color: '#000000',
            textAlign: 'left',
            placeHolder: 'Paste your ethereum address here',
            placeHolderColor: '#989898'
        });

        this.input_amount = new EditBox({
            type: 'number',
            fontFamily: 'Alphakind',
            fontSize: 16,
            posX: this.canvas_width / 2 - 220.0,
            posY: this.canvas_height / 2 + 11.0,
            width: 180,
            height: 45,
            color: '#000000',
            textAlign: 'left'
        });

        this.btn_deposit = this.addChild(this.createButton(resource.deposit_nor, this.canvas_width / 2 - 124.0, this.canvas_height / 2 - 190.0, 0.5, 0.5, 1.0, this.onDeposit));
        this.btn_withdraw = this.addChild(this.createButton(resource.withdraw_nor, this.canvas_width / 2 + 126.0, this.canvas_height / 2 - 190.0, 0.5, 0.5, 1.0, this.onWithdraw));
        this.btn_withdraw_send = this.addChild(this.createButton(resource.withdraw_send, this.canvas_width / 2, this.canvas_height / 2 + 115.0, 0.5, 0.5, 1.0, this.onWithdrawSend));
        this.btn_copy_address = this.addChild(this.createButton(resource.copy_address, this.canvas_width / 2 + 227.0, this.canvas_height / 2 + 8.0, 0.5, 0.5, 1.0, this.onCopyAddress));
        this.btn_cancel = this.addChild(this.createButton(resource.btn_close, this.canvas_width / 2 + 365.0, this.canvas_height / 2 - 227.0, 0.5, 0.5, 1.0, this.onClose));

        this.input_address.show(false);
        this.input_amount.show(false);
        
        this.visible = false;
    }

    onResize(width, height) {
        super.onResize(width, height);

        this.updateRenderer();
    }

    updateRenderer() {
        const scale_x = this.canvas_width / GAME_WIDTH;
        const scale_y = this.canvas_height / GAME_HEIGHT;
        const scale = Math.min(scale_x, scale_y);

        if(this.spr_bg !== null) {
            this.spr_bg.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 55.5 * scale);
            this.spr_bg.scale.set(scale);
        }

        if(this.spr_deposit_act !== null) {
            this.spr_deposit_act.setTransform(this.canvas_width / 2 - 124.0 * scale, this.canvas_height / 2 - 190.0 * scale);
            this.spr_deposit_act.scale.set(scale);
        }

        if(this.spr_withdraw_act !== null) {
            this.spr_withdraw_act.setTransform(this.canvas_width / 2 + 126.0 * scale, this.canvas_height / 2 - 190.0 * scale);
            this.spr_withdraw_act.scale.set(scale);
        }

        if(this.spr_bg_address !== null) {
            if(this.mode === PAYMENT.DEPOSIT)
                this.spr_bg_address.setTransform(this.canvas_width / 2, this.canvas_height / 2 + 7.5 * scale);
            else
                this.spr_bg_address.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 42.5 * scale);
            this.spr_bg_address.scale.set(scale);
        }

        if(this.spr_bg_token !== null) {
            this.spr_bg_token.setTransform(this.canvas_width / 2 - 157.5 * scale, this.canvas_height / 2 + 33.5 * scale);
            this.spr_bg_token.scale.set(scale);
        }

        if(this.spr_token !== null) {
            this.spr_token.setTransform(this.canvas_width / 2 - 255.0 * scale, this.canvas_height / 2 + 33.5 * scale);
            this.spr_token.scale.set(scale);
        }

        if(this.spr_ether !== null) {
            if(this.mode === PAYMENT.DEPOSIT)
                this.spr_ether.setTransform(this.canvas_width / 2 - 268.0 * scale, this.canvas_height / 2 - 60.0 * scale);
            else
                this.spr_ether.setTransform(this.canvas_width / 2 - 268.0 * scale, this.canvas_height / 2 - 110.0 * scale);
            this.spr_ether.scale.set(scale);
        }

        if(this.spr_symbol !== null) {
            this.spr_symbol.setTransform(this.canvas_width / 2 - 230.5 * scale, this.canvas_height / 2 + 33.5 * scale);
            this.spr_symbol.scale.set(scale);
        }

        if(this.spr_deposit_desc !== null) {
            this.spr_deposit_desc.setTransform(this.canvas_width / 2 - 193.5 * scale, this.canvas_height / 2 - 24.5 * scale);
            this.spr_deposit_desc.scale.set(scale);
        }

        if(this.spr_withdraw_desc !== null) {
            this.spr_withdraw_desc.setTransform(this.canvas_width / 2 - 193.5 * scale, this.canvas_height / 2 - 74.5 * scale);
            this.spr_withdraw_desc.scale.set(scale);
        }

        if(this.spr_withdraw_amount !== null) {
            this.spr_withdraw_amount.setTransform(this.canvas_width / 2 - 213.0 * scale, this.canvas_height / 2 + 1.0 * scale);
            this.spr_withdraw_amount.scale.set(scale);
        }

        if(this.lbl_deposit !== null) {
            this.lbl_deposit.setTransform(this.canvas_width / 2 - 245.0 * scale, this.canvas_height / 2 - 60.0 * scale);
            this.lbl_deposit.scale.set(scale);
        }

        if(this.lbl_withdraw !== null) {
            this.lbl_withdraw.setTransform(this.canvas_width / 2 - 245.0 * scale, this.canvas_height / 2 - 110.0 * scale);
            this.lbl_withdraw.scale.set(scale);
        }

        if(this.lbl_deposit_address !== null) {
            this.lbl_deposit_address.setTransform(this.canvas_width / 2 - 269.0 * scale, this.canvas_height / 2 + 7.5 * scale);
            this.lbl_deposit_address.scale.set(scale);
        }

        if(this.input_address !== null) {
            this.input_address.setTransform(this.canvas_width / 2 - 269.0 * scale, this.canvas_height / 2 - 65.0 * scale);
            this.input_address.setScale(scale);
        }

        if(this.input_amount !== null) {
            this.input_amount.setTransform(this.canvas_width / 2 - 220.0 * scale, this.canvas_height / 2 + 11.0 * scale);
            this.input_amount.setScale(scale);
        }

        if(this.btn_deposit !== null) {
            this.btn_deposit.setTransform(this.canvas_width / 2 - 124.0 * scale, this.canvas_height / 2 - 190.0 * scale);
            this.btn_deposit.scale.set(scale);
        }

        if(this.btn_withdraw !== null) {
            this.btn_withdraw.setTransform(this.canvas_width / 2 + 126.0 * scale, this.canvas_height / 2 - 190.0 * scale);
            this.btn_withdraw.scale.set(scale);
        }

        if(this.btn_withdraw_send !== null) {
            this.btn_withdraw_send.setTransform(this.canvas_width / 2, this.canvas_height / 2 + 115.0 * scale);
            this.btn_withdraw_send.scale.set(scale);
        }

        if(this.btn_copy_address !== null) {
            this.btn_copy_address.setTransform(this.canvas_width / 2 + 227.0 * scale, this.canvas_height / 2 + 8.0 * scale);
            this.btn_copy_address.scale.set(scale);
        }

        if(this.btn_cancel !== null) {
            this.btn_cancel.setTransform(this.canvas_width / 2 + 365.0 * scale, this.canvas_height / 2 - 227.0 * scale);
            this.btn_cancel.scale.set(scale);
        }
    }

    onDeposit = () => {
        this.show(PAYMENT.DEPOSIT);
    }

    onWithdraw = () => {
        this.show(PAYMENT.WITHDRAW);
    }

    onWithdrawSend = async () => {
        const address = this.input_address.getValue();
        const amount = this.input_amount.getValue();

        if(address === '')
            return toast.error('Please input your wallet address.');

        if(amount === '')
            return toast.error('Please input withdraw amount.');

        const user = dataManager.getUser();
        if(user === null)
            return;

        const response = await apiManager.userWithdraw({
            userid: user.userId,
            address: address,
            amount: Number(amount)
        });

        if(!response.data.status)
            return toast.error(response.data.message);

        toast.success('Withdraw successfully completed!');

        dataManager.setUserBalance(response.data.balance);
        gameApp.updateUser();
    }

    onCopyAddress = () => {
        window.navigator.clipboard.writeText(this.lbl_deposit_address.text);
    }

    onClose = () => {
        this.input_address.setValue('');
        this.input_amount.setValue('');
        this.input_address.show(false);
        this.input_amount.show(false);
        this.visible = false;
    }

    show = (mode = PAYMENT.DEPOSIT) => {
        this.mode = mode;
        this.visible = true;

        this.spr_deposit_act.visible = (mode === PAYMENT.DEPOSIT);
        this.spr_withdraw_act.visible = (mode === PAYMENT.WITHDRAW);
        this.spr_deposit_desc.visible = (mode === PAYMENT.DEPOSIT);
        this.spr_withdraw_desc.visible = (mode === PAYMENT.WITHDRAW);
        this.spr_withdraw_amount.visible = (mode === PAYMENT.WITHDRAW);
        this.spr_bg_token.visible = (mode === PAYMENT.WITHDRAW);
        this.spr_token.visible = (mode === PAYMENT.WITHDRAW);
        this.spr_symbol.visible = (mode === PAYMENT.WITHDRAW);

        this.lbl_deposit.visible = (mode === PAYMENT.DEPOSIT);
        this.lbl_withdraw.visible = (mode === PAYMENT.WITHDRAW);
        this.lbl_deposit_address.visible = (mode === PAYMENT.DEPOSIT);

        this.input_address.show(mode === PAYMENT.WITHDRAW);
        this.input_amount.show(mode === PAYMENT.WITHDRAW);

        this.btn_deposit.visible = (mode !== PAYMENT.DEPOSIT);
        this.btn_withdraw.visible = (mode !== PAYMENT.WITHDRAW);
        this.btn_withdraw_send.visible = (mode === PAYMENT.WITHDRAW);
        this.btn_copy_address.visible = (mode === PAYMENT.DEPOSIT);

        const user = dataManager.getUser();
        if(user !== null)
            this.lbl_deposit_address.text = user.pumpaddress;

        this.updateRenderer();
    }
}