import * as PIXI from "pixi.js";

export class Button extends PIXI.Sprite {

    target = null;

    static from(texture, x, y, anchor_x, anchor_y, scale, target) {
        return new Button(texture, x, y, anchor_x, anchor_y, scale, target);
    }

    constructor(texture, x, y, anchor_x, anchor_y, scale, target) {
        super(texture);

        this.target = target;

        this.setTransform(x, y);
        this.anchor.set(anchor_x, anchor_y);
        this.scale.set(scale);

        this.addListeners();
    }

    addListeners () {
        this.eventMode = 'static';
        this.cursor = 'pointer';
        
        this.addListener('pointerdown', () => {
            this.scale.set(this.scale.x * 1.05, this.scale.y * 1.05);
        });

        this.addListener('pointerup', () => {
            this.scale.set(this.scale.x / 1.05, this.scale.y / 1.05);
            return this.target();
        });

        this.addListener('pointerupoutside', () => {
            this.scale.set(this.scale.x / 1.05, this.scale.y / 1.05);
        });
    }
}