import { BET_1X } from "../view/game/data/Constant";

class DataManager {

    user            = null;
    online_users    = 0;

    round           = null;
    histories       = null;

    bet             = null;
    is_bet          = false;

    socket          = null;

    constructor() {
        this.bet = {
            food: -1,
            amount: BET_1X
        }
    }

    setUser(user) {
        this.user = user;
    }

    setUserBalance(balance) {
        this.user.balance = balance;
    }

    getUser() {
        return this.user;
    }

    setOnlineUsers(users) {
        this.online_users = users;
    }

    getOnlineUsers() {
        return this.online_users;
    }

    setRound(round) {
        this.round = round;
    }

    getRound() {
        return this.round;
    }

    setHistories(histories) {
        this.histories = histories;
    }

    getHistories() {
        return this.histories;
    }

    setBetAmount(amount) {
        this.bet.amount = amount;
    }

    getBetAmount() {
        return this.bet.amount;
    }

    setBetFood(food) {
        this.bet.food = food;
    }

    getBetFood() {
        return this.bet.food;
    }

    setBet(bet) {
        if(bet === null)
            return;
        
        this.bet.amount = bet.amount;
        this.bet.food = bet.pick;

        this.play();
    }

    getBet() {
        return this.bet;
    }

    play() {
        this.is_bet = true;
    }

    unPlay() {
        this.is_bet = false;
        this.bet.food = -1;
    }

    isPlayed() {
        return this.is_bet;
    }

    setSocket(socket) {
        this.socket = socket;
    }

    getSocket() {
        return this.socket;
    }
}

const dataManager = new DataManager();

export default dataManager;