
import { BaseLayer } from "./BaseLayer";
import { GAME_HEIGHT, GAME_WIDTH } from "../data/Constant";

export class HelpLayer extends BaseLayer {

    spr_bg          = null;
    btn_cancel      = null;

    init(resource) {
        super.init(resource);

        this.spr_bg = this.addChild(this.createSprite(resource.bg_help, this.canvas_width / 2, this.canvas_height / 2 - 64.5, 0.5, 0.5));
        this.btn_cancel = this.addChild(this.createButton(resource.btn_close, this.canvas_width / 2 + 370.0, this.canvas_height / 2 - 220.0, 0.5, 0.5, 1.0, this.onClose));

        this.visible = false;
    }

    onResize(width, height) {
        super.onResize(width, height);

        this.updateRenderer();
    }

    updateRenderer() {
        const scale_x = this.canvas_width / GAME_WIDTH;
        const scale_y = this.canvas_height / GAME_HEIGHT;
        const scale = Math.min(scale_x, scale_y);

        if(this.spr_bg !== null) {
            this.spr_bg.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 64.5 * scale);
            this.spr_bg.scale.set(scale);
        }

        if(this.btn_cancel !== null) {
            this.btn_cancel.setTransform(this.canvas_width / 2 + 370.0 * scale, this.canvas_height / 2 - 220.0 * scale);
            this.btn_cancel.scale.set(scale);
        }
    }

    onClose = () => {
        this.visible = false;
    }
}