import axios from 'axios';
import Config from '../config';
import toast from 'react-hot-toast';
import dataManager from './DataManager';
import socketManager from './SocketManager';

class APIManager {
    constructor() {
        axios.interceptors.request.use(
            config => {
                config.baseURL = Config.API.Url;
                config.headers = {
                    // authorization: `Bearer ${this.getToken()}`
                };
                return config;
            },
            error => Promise.reject(error)
        );

        axios.interceptors.response.use(
            response => {
                if (!response.data.status && response.data.session) {
                    alert(response.data.message);
                    setTimeout(() => {
                        window.location.assign("/");
                    }, 500);
                }
                return response;
            },
            error => Promise.reject(error)
        );
    }

    userConnect = async () => {
        if(!window.ethereum) {
            toast.error('Please install metamask');
            return false;
        }

        try {
            const result = await window.ethereum.request({ method: 'eth_requestAccounts' });
            const response = await axios.post(Config.API.Request.userConnect, {address: result[0]});
            if(!response.data.status) {
                toast.error(response.data.message);
                return false;
            }

            dataManager.setUser({
                userId: response.data.userid, 
                address: response.data.address, 
                pumpaddress: response.data.pumpaddress, 
                balance: response.data.balance
            });

            dataManager.setSocket(response.data.socket);
            socketManager.connect();
            
            return true;
        }
        catch(err) {
            toast.error(err.message);
            return false;
        }
    }

    userWithdraw = async(data) => {
        const response = await axios.post(Config.API.Request.userWithdraw, data);
        return response;
    }
}

const apiManager = new APIManager();

export default apiManager;