import { GAME_HEIGHT, GAME_WIDTH } from "../data/Constant";

export class EditBox {

    element             = null;
    type                = null;
    top                 = null;
    topOffset           = null;
    left                = null;
    leftOffset          = null;
    width               = null;
    height              = null;
    background          = null;
    color               = null;
    fontSize            = null;
    fontFamily          = null;
    placeHolder         = null;
    placeHolderColor    = null;
    textAlign           = null;
    attr                = null;
    lists               = null;
    callback            = null;
    blur                = null;
    
    constructor(options) {
        options = options || {};

        this.element = null;
        this.type = typeof options.type !== 'undefined' ? options.type : 'input';
        this.top = typeof options.posY !== 'undefined' ? options.posY : 0;
        this.topOffset = typeof options.topOffset !== 'undefined' ? options.topOffset : true;
        this.left = typeof options.posX !== 'undefined' ? options.posX : 0;
        this.leftOffset = typeof options.leftOffset !== 'undefined' ? options.leftOffset : true;
        this.width = typeof options.width !== 'undefined' ? options.width : 'initial';
        this.height = typeof options.height !== 'undefined' ? options.height : 'auto';
        this.background = typeof options.background !== 'undefined' ? options.background : 'transparent';
        this.color = typeof options.color !== 'undefined' ? options.color : '#000000';
        this.fontSize = typeof options.fontSize !== 'undefined' ? options.fontSize : 24;
        this.fontFamily = typeof options.fontFamily !== 'undefined' ? options.fontFamily : 'Arial';
        this.placeHolder = typeof options.placeHolder !== 'undefined' ? options.placeHolder : '';
        this.placeHolderColor = typeof options.placeHolderColor !== 'undefined' ? options.placeHolderColor : '#888888';
        this.textAlign = typeof options.textAlign !== 'undefined' ? options.textAlign : 'left';
        this.attr = typeof options.attr !== 'undefined' ? options.attr : {};
        this.lists = typeof options.lists !== 'undefined' ? options.lists : [];
        this.callback = typeof options.callback !== 'undefined' ? options.callback : null;
        this.blur = typeof options.blur !== 'undefined' ? options.blur : null;

        this.init();
    }

    init = () => {        
        let w = window, d = document, e = d.documentElement, g = d.getElementsByTagName('body')[0];
        let viewPort = {
            width: w.innerWidth || e.clientWidth || g.clientWidth,
            height: w.innerHeight || e.clientHeight || g.clientHeight
        };

        let scale_x = viewPort.width / GAME_WIDTH;
        let scale_y = viewPort.height / GAME_HEIGHT;
        let scale = Math.min(scale_x, scale_y);

        let para = null;
        if(this.type === 'textarea') {
            para = document.createElement('textarea');
            para.style.resize = 'none';
            para.style.overflow = 'hidden';   
        }
        else if(this.type === 'file') {
            para = document.createElement('input');
            para.setAttribute('type', 'file');
            para.style.display = 'none';
            para.addEventListener("change", this.callback); 
        }
        else if(this.type === 'select') {
            para = document.createElement('select');     
            para.style.overflow = 'hidden';  
            var html_str = '';
            for(var i in this.lists){
                html_str += '<option value="'+this.lists[i]+'">'+this.lists[i]+'</option>';
            }
            para.innerHTML = html_str;
        }
        else if(this.type === 'password') {
            para = document.createElement('input');
            para.setAttribute('type', 'password');
        }
        else if(this.type === 'number') {
            para = document.createElement('input');
            para.setAttribute('type', 'number');
            para.setAttribute('min', 0);
            para.setAttribute('step', 1);
            para.setAttribute('onkeypress', "return (event.charCode == 8 || event.charCode == 0 || event.charCode == 13) ? null : event.charCode >= 48 && event.charCode <= 57");
        }
        else {
            para = document.createElement('input');
            para.setAttribute('type', 'text');
        }

        para.style.width = this.width * scale + 'px';
        para.style.height = this.height * scale +'px';   
        para.style.padding = 0;
        para.style.textAlign = this.textAlign;
        para.setAttribute('placeholder', this.placeHolder);

        let body = document.getElementsByTagName('body');
        body[0].appendChild(para);

        this.element = para;
        this.element.style.position = 'absolute';
        this.element.style.fontSize = (this.fontSize * scale)+'px';

        if(this.topOffset)
            this.element.style.top = this.top * scale +'px';
        else
            this.element.style.bottom = this.top * scale +'px';

        this.element.style.left = this.left * scale + 'px';

        this.element.style.backgroundColor = 'transparent';
        this.element.style.border = '0px';
        this.element.style.fontFamily = this.fontFamily;
        this.element.style.color = this.color;

        for(var key in this.attr)
            this.element.setAttribute(key, this.attr[key]);

        this.element.addEventListener("focus", function () {
            this.style.outline = "none";  
        });

        this.element.addEventListener("blur", () => {
            if(this.blur !== null)
                return this.blur(this.element);
        });
    }

    setTransform = (x, y) => {
        this.top = y;
        this.left = x;

        if(this.topOffset)
            this.element.style.top = this.top +'px';
        else
            this.element.style.bottom = this.top +'px';

        this.element.style.left = this.left + 'px';
    }
    
    setScale = (scale) => {
        this.element.style.width = this.width * scale + 'px';
        this.element.style.height = this.height * scale +'px'; 
        this.element.style.fontSize = (this.fontSize * scale) + 'px';  
    }

    getValue = () => {
        if(this.type == 'file'){
            return this.element.files[0];
        }
        return this.element.value;
    }

    setValue = (val) => {
        this.element.value = val;
    }

    disable = (value) => {
        this.element.disabled = value;
    }

    show = (value) => {
        if(value)
            this.element.removeAttribute('hidden');
        else
            this.element.setAttribute('hidden', 'hidden');
    }

    destroy = () =>  {
        this.element.remove();
    }

    isExist = () => {
        return this.element?true:false;
    }
}