
import { BaseLayer } from "./BaseLayer";
import { GAME_HEIGHT, GAME_WIDTH } from "../data/Constant";

const MODE = {
    TOTAL: 0,
    SUSHI: 1,
    PIZZA: 2,
    ICECREAM: 3
}

export class HintLayer extends BaseLayer {

    spr_total       = null;
    spr_sushi       = null;
    spr_pizza       = null;
    spr_icecream    = null;
    btn_cancel      = null;
    btn_sushi       = null;
    btn_pizza       = null;
    btn_icecream    = null;

    init(resource) {
        super.init(resource);

        this.spr_total = this.addChild(this.createSprite(resource.bg_total, this.canvas_width / 2, this.canvas_height / 2 - 64.5, 0.5, 0.5));
        this.spr_sushi = this.addChild(this.createSprite(resource.bg_sushi, this.canvas_width / 2, this.canvas_height / 2 - 64.5, 0.5, 0.5));
        this.spr_pizza = this.addChild(this.createSprite(resource.bg_pizza, this.canvas_width / 2, this.canvas_height / 2 - 64.5, 0.5, 0.5));
        this.spr_icecream = this.addChild(this.createSprite(resource.bg_icecream, this.canvas_width / 2, this.canvas_height / 2 - 64.5, 0.5, 0.5));
        this.btn_cancel = this.addChild(this.createButton(resource.btn_close, this.canvas_width / 2 + 370.0, this.canvas_height / 2 - 220.0, 0.5, 0.5, 1.0, this.onClose));
        this.btn_sushi = this.addChild(this.createButton(resource.btn_hint_detail, this.canvas_width / 2, this.canvas_height / 2 - 177.0, 0.5, 0.5, 1.0, this.onSushi));
        this.btn_pizza = this.addChild(this.createButton(resource.btn_hint_detail, this.canvas_width / 2, this.canvas_height / 2 - 62.0, 0.5, 0.5, 1.0, this.onPizza));
        this.btn_icecream = this.addChild(this.createButton(resource.btn_hint_detail, this.canvas_width / 2, this.canvas_height / 2 + 53.0, 0.5, 0.5, 1.0, this.onIcecream));
        this.btn_sushi.alpha = 0;
        this.btn_pizza.alpha = 0;
        this.btn_icecream.alpha = 0;
        
        this.visible = false;
    }

    onResize(width, height) {
        super.onResize(width, height);

        this.updateRenderer();
    }

    updateRenderer() {
        const scale_x = this.canvas_width / GAME_WIDTH;
        const scale_y = this.canvas_height / GAME_HEIGHT;
        const scale = Math.min(scale_x, scale_y);

        if(this.spr_total !== null) {
            this.spr_total.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 64.5 * scale);
            this.spr_total.scale.set(scale);
        }

        if(this.spr_sushi !== null) {
            this.spr_sushi.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 64.5 * scale);
            this.spr_sushi.scale.set(scale);
        }

        if(this.spr_pizza !== null) {
            this.spr_pizza.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 64.5 * scale);
            this.spr_pizza.scale.set(scale);
        }

        if(this.spr_icecream !== null) {
            this.spr_icecream.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 64.5 * scale);
            this.spr_icecream.scale.set(scale);
        }

        if(this.btn_cancel !== null) {
            this.btn_cancel.setTransform(this.canvas_width / 2 + 370.0 * scale, this.canvas_height / 2 - 220.0 * scale);
            this.btn_cancel.scale.set(scale);
        }

        if(this.btn_sushi !== null) {
            this.btn_sushi.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 177.0 * scale);
            this.btn_sushi.scale.set(scale);
        }

        if(this.btn_pizza !== null) {
            this.btn_pizza.setTransform(this.canvas_width / 2, this.canvas_height / 2 - 62.0 * scale);
            this.btn_pizza.scale.set(scale);
        }

        if(this.btn_icecream !== null) {
            this.btn_icecream.setTransform(this.canvas_width / 2, this.canvas_height / 2 + 53.0 * scale);
            this.btn_icecream.scale.set(scale);
        }
    }

    onClose = () => {
        this.visible = false;
    }

    onSushi = () => {
        this.show(MODE.SUSHI);
    }

    onPizza = () => {
        this.show(MODE.PIZZA);
    }

    onIcecream = () => {
        this.show(MODE.ICECREAM);
    }

    show = (mode = MODE.TOTAL) => {
        this.visible = true;

        this.btn_cancel.visible = true;
        this.spr_total.visible = (mode === MODE.TOTAL);
        this.spr_sushi.visible = (mode === MODE.SUSHI);
        this.spr_pizza.visible = (mode === MODE.PIZZA);
        this.spr_icecream.visible = (mode === MODE.ICECREAM);
        this.btn_sushi.visible = (mode === MODE.TOTAL);
        this.btn_pizza.visible = (mode === MODE.TOTAL);
        this.btn_icecream.visible = (mode === MODE.TOTAL);
    }
}