import * as PIXI from "pixi.js";
import { GameLayer } from "./layer/GameLayer";
import { ResultLayer } from "./layer/ResultLayer";
import { TopLayer } from "./layer/TopLayer";
import { HelpLayer } from "./layer/HelpLayer";
import { Resource } from './data/Resource';
import { FRAME_RATE } from './data/Constant';
import { HintLayer } from "./layer/HintLayer";
import dataManager from "../../manager/DataManager";
import { PaymentLayer } from "./layer/PaymentLayer";

export class FoodApp extends PIXI.Application {

    canvas_width    = 0;
    canvas_height   = 0;

    layer_game      = null;
    layer_result    = null;
    layer_top       = null;
    layer_hint      = null;
    layer_help      = null;
    layer_payment   = null;

    speed_delta     = 0;

    constructor(option) {
        super(option);
    }

    onResize(width, height) {
        if(this.canvas_width === width && this.canvas_height === height)
            return;

        this.canvas_width = width;
        this.canvas_height = height;

        if(this.layer_game !== null)
            this.layer_game.onResize(width, height);

        if(this.layer_result !== null)
            this.layer_result.onResize(width, height);

        if(this.layer_top !== null)
            this.layer_top.onResize(width, height);

        if(this.layer_hint !== null)
            this.layer_hint.onResize(width, height);

        if(this.layer_help !== null)
            this.layer_help.onResize(width, height);

        if(this.layer_payment !== null)
            this.layer_payment.onResize(width, height);

        this.renderer.resize(width, height);
    }

    onFocus() {
        this.updateUser();
        this.updateGame();
        this.updateHistories();
        this.updatePick(dataManager.getBetFood());
    }

    onBlur() {
        
    }

    startGame() {
        this.stage.sortableChildren = true;
        this.frame_speed = 60 / FRAME_RATE;

        this.layer_game = new GameLayer();
        this.stage.addChild(this.layer_game);

        this.layer_result = new ResultLayer();
        this.stage.addChild(this.layer_result);

        this.layer_top = new TopLayer();
        this.stage.addChild(this.layer_top);

        this.layer_hint = new HintLayer();
        this.stage.addChild(this.layer_hint);

        this.layer_help = new HelpLayer();
        this.stage.addChild(this.layer_help);

        this.layer_payment = new PaymentLayer();
        this.stage.addChild(this.layer_payment);

        const keys = [];
        Resource.forEach(resource => {
            PIXI.Assets.add({alias: resource.key, src: resource.src});
            keys.push(resource.key);
        });

        PIXI.Assets.load(keys)
        .then((textures) => {
            if(this.layer_game !== null)
                this.layer_game.init(textures);

            if(this.layer_result !== null)
                this.layer_result.init(textures);

            if(this.layer_top !== null)
                this.layer_top.init(textures);

            if(this.layer_hint !== null)
                this.layer_hint.init(textures);

            if(this.layer_help !== null)
                this.layer_help.init(textures);

            if(this.layer_payment !== null)
                this.layer_payment.init(textures);
        });
    }

    render(delta) {
        this.speed_delta += delta;
        let frame_count = 0;
        let frame_speed = 60 / FRAME_RATE;

        while(this.speed_delta >= frame_speed) {
            this.speed_delta -= frame_speed;
            frame_count++;
        }

        if(frame_count !== 0)
            super.render();
    }

    destroy(bRemoveView, bStageOptions) {
        super.destroy(bRemoveView, bStageOptions);
    }

    updateOnlineUsers() {
        if(this.layer_top !== null)
            this.layer_top.updateOnlineUsers();
    }

    updateUser() {
        if(this.layer_game !== null)
            this.layer_game.updateUser();
    }

    updateGame(isFirst = false) {
        if(this.layer_game !== null)
            this.layer_game.updateGame(isFirst);
    }

    updateHistories() {
        if(this.layer_game !== null)
            this.layer_game.updateHistories();
    }

    updatePick(index) {
        if(this.layer_game !== null)
            this.layer_game.updatePick(index);
    }

    showResult(result, value) {
        if(this.layer_result !== null)
            this.layer_result.show(result, value);
    }

    hideResult() {
        if(this.layer_result !== null)
            this.layer_result.hide();
    }

    showHelp() {
        if(this.layer_help !== null)
            this.layer_help.visible = true;
    }

    showHint() {
        if(this.layer_hint !== null)
            this.layer_hint.show();
    }

    showPayment() {
        if(this.layer_payment !== null)
            this.layer_payment.show();
    }
}