import { io } from 'socket.io-client';
import toast from 'react-hot-toast';
import dataManager from "./DataManager";
import { gameApp } from "../view/game";

class SocketManager {

    socket = null;

    constructor() {
        
    };

    connect() {
        const socket = dataManager.getSocket();
        if(socket === null)
            return;

        this.socket = io(socket.host, { transports: ['websocket'] });

        let self = this;
        this.socket.on('connect', () => {
            self.verify();
        });

        this.socket.on('online_users', (data) => {
            dataManager.setOnlineUsers(data.total);
            gameApp.updateOnlineUsers();
        });

        this.socket.on('round', (data) => {
            dataManager.setRound(data);
            gameApp.updateGame();
        });

        this.socket.on('round_history', (data) => {
            dataManager.setHistories(data);
            gameApp.updateHistories();
        });

        this.socket.on('update_balance', (data) => {
            dataManager.setUserBalance(data.balance);
            gameApp.updateUser();
        });

        this.socket.on('deposit_result', (data) => {
            if(data.result)
                toast.success('deposit was done successfully.');
            else
                toast.error('deposit was failed.');
        });

        this.socket.on('bonus_balance', (data) => {
            toast.success('You got ' + data.bonus + ' bonus foods from admin.');
            dataManager.setUserBalance(Number(data.balance));
            gameApp.updateUser();
        });
    }

    verify() {
        const user = dataManager.getUser();
        if(user === null)
            return;

        this.socket.emit('verify', {userId: user.userId}, (response) => {
            if(response.status !== 'success')
                return;

            dataManager.setRound(response.round);
            dataManager.setHistories(response.histories);

            gameApp.updateUser();
            gameApp.updateGame(true);
            gameApp.updateHistories();

            dataManager.setBet(response.bet);
            gameApp.updateGame();
            gameApp.updatePick(dataManager.getBetFood());
        });
    }

    bet() {
        const user = dataManager.getUser();
        if(user === null)
            return;

        const bet = dataManager.getBet();
        if(bet.food === -1)
            return;

        const bet_request = {
            userId: user.userId,
            bet: bet
        }

        this.socket.emit('bet', bet_request, (response) => {
            if(response.status) {
                dataManager.play();
                gameApp.updateGame();
            }
            else {
                toast.error(response.message);
            }
        });
    }
}

const socketManager = new SocketManager();

export default socketManager;