import { useEffect, useRef } from "react";
import '../../assets/css/gameview.css';
import '../../assets/css/fonts.css';
import { FoodApp } from "./FoodApp";

export var gameApp = null;

const GameView = () => {
    const gameRef = useRef(null)

    useEffect(() => {
        window.addEventListener('resize', resizeHandler);
        document.addEventListener('visibilitychange', visibilityHandler);

        gameApp = new FoodApp({
            backgroundColor: 0xEFEAE1,
            backgroundAlpha: 1.0,
            antialiasing: true,
            autoDensity: true
        });

        gameRef.current.appendChild(gameApp.view);
        gameApp.startGame();

        resizeHandler();

        return () => {
            window.removeEventListener('resize', resizeHandler);
        }
    }, []);

    const resizeHandler = () => {
        if(!gameApp)
            return;

        const parent = document.getElementById('gameContainer');

        // if(parent.clientWidth < parent.clientHeight) {
        //     window.screen.orientation.lock('landscape');
        //     return;
        // }

        gameApp.onResize(parent.clientWidth, parent.clientHeight);
    }

    const visibilityHandler = () => {
        if(document.visibilityState === 'visible')
            gameApp.onFocus();
        else
            gameApp.onBlur();
    }

    return (
        <div id="gameContainer" ref={gameRef}>
        </div>
    )
}

export default GameView;